.App {
  font-family: Arial, sans-serif;
  max-width: 500px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fffbea;
  border-radius: 8px;
  box-shadow: 0 0 10px #ddd;
}

.booking-form div {
  margin-bottom: 1rem;
}

.booking-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.3rem;
}

.booking-form input {
  width: 100%;
  padding: 0.4rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.booking-form button {
  padding: 0.6rem 1.2rem;
  background: #f4d35e;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

.booking-form button:hover {
  background: #f7e26b;
}
